    //pt za measurement
    //a4 za format
    var doc = new jsPDF("l","pt","a4");
    doc.html(document.querySelector("#vqTest"),{
      callback: function(pdf){
        pdf.save("mypdf.pdf");
      }
    });
  },
  makePDF360(){
    window.html2canvas=html2canvas;
    //p = portrait (moze i l za landscape)
    //pt za measurement
    //a4 za format
    var doc = new jsPDF("l","pt","a4");
    doc.html(document.querySelector("#feedbackPDF"),{
      callback: function(pdf){
        pdf.save("mypdf.pdf");
      }
    });
  },
  makePDFBelvin(){
